.App {
  text-align: center;
}

.black-nav {
  font-family: "SOYOMapleBoldTTF";
  font-size: 200%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: black;
  width: 100%;
  height: 15vh;
  color: white;
  flex-direction: column;
}

.TimeText {
  font-family: "SOYOMapleBoldTTF";
  font-size: 70%;
}

.menu-panel {
  font-family: "SOYOMapleBoldTTF";
  font-size: 150%;
  text-align: center;
  line-height: 200%;
  background: linear-gradient(45deg, rgb(255, 247, 217), rgb(178, 255, 105));
  width: 100%;
  padding-top: 5vh;
  height: 80vh;
  color: black;
}

div {
  white-space: pre-wrap;
}

@font-face {
  font-family: 'SOYOMapleBoldTTF';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2310@1.0/SOYOMapleBoldTTF.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}

body {
  -webkit-user-select:none;
  -moz-user-select:none;
  -ms-user-select:none;
  user-select:none
}